<template>
	<view>
		<button type="warn" @click='onlogin'>微信登录</button>
		<button type="warn" @click='getData'>微录</button>
		kjh
	</view>
</template>

<script>
	// import axios from 'axios'
	import request from '../../utools/request.js'
	export default {
		data() {
			return {
             
			}
		},
		methods: {
			async getData(){
				console.log(uni.getStorageSync('token')==false);
				let r=await request('/gg')
				console.log(r);
			    
			},
			onlogin() {
				uni.login({
					provider: 'weixin',
					onlyAuthorize:true,
					success: function(loginRes) {
						// console.log(loginRes);
						
						uni.request({
						    url: 'http://localhost:3000/onLogin', //仅为示例，并非真实接口地址。
						    data: {
						        code: loginRes.code
						    },
						    
						    success: (res) => {
						        console.log(res.data);
								uni.setStorageSync('token',res.data.token)
						        
						    },
							fail:(res)=>{
								console.log(res);
							}
						});
					}
				});
			}
           }



	
	}
</script>

